@import url(https://fonts.googleapis.com/css?family=Roboto:400,500,700&display=swap);
*{
    margin: 0;
    padding: 0;
    outline: 0;
    box-sizing: border-box;
    font-family: Roboto, sans-serif;
}

input, button{
    font-family: Roboto, sans-serif;
}

html, body, #root {
    height: 100%;
}

body {
    background-color: #e5e6f0;
    -webkit-font-smoothing: antialiased;

}
#app{
    width: 100%;
    height: 100%;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
}



aside {
    background: rgb(17, 52, 56);
    box-shadow: 0 0 14px 0 rgba(0, 0, 0, 0.205);
    border-radius: 2px;
    padding: 30px 20px;
    position: relative;
    color: white;
}

aside nav ul {
    width: 100%;
}



aside nav ul li {
    width: 100%;
    list-style: none;
    padding: 15px 20px;
    font-weight: bold;
    letter-spacing: 1px;
    font-size: 18px;
    border-bottom: 1px solid #d3d3d3;
    transition: all .4s;
}

aside nav ul li:hover{
    background-color: rgb(117, 117, 117);
    border-radius: 5px;
    cursor: pointer;
}

.footer-aside{
    left: 0;
    bottom: 0;
    width: 100%;
    height: 30px;
    background-color: darkslategrey;
    position: absolute;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 100%;
    gap: 100%;
   
}
.footer-aside h1 {
    font-weight: bold;
    letter-spacing: 1px;
    font-size: 20px;
    margin-top: 5px;
    cursor: pointer;
    text-align: center;
    color: rgba(255, 250, 240, 0.582);
}



.close-menu {
    width: 40px;
    height: 30px;
    position: absolute;
    background-color: darkslategrey;
    bottom: 0;
    right: -40px; 
    font-size: 25px;
    text-align: center;
    color: rgba(255, 250, 240, 0.582);
    cursor: pointer;
    z-index: 5;
}

.hidden {
    padding: 0;
}

.close{
   display: none;
   visibility: hidden;
}

.map-el-close{
    display: none;
    visibility: hidden;
}

.cad-hidden{
    display: none;
    visibility: hidden;
}

section.form {
    width: 100%;
    max-width: 400px;
    margin: 0 auto;
    margin-top: 40px;
}

section.form h1{
    text-align: center;
    font-size: 25px;
    color: black;
    margin-bottom: 30px;
}
section.form input{
    width: 100%;
    height: 50px;
    border: 0;
    padding: 5px 10px;
    border-radius: 4px;
    font-size: 15px;
}

input::-webkit-input-placeholder{
    color: #d3d3d3;
}

input:-ms-input-placeholder{
    color: #d3d3d3;
}

input::-ms-input-placeholder{
    color: #d3d3d3;
}

input::placeholder{
    color: #d3d3d3;
}

section.form input + input {
    margin-top: 20px;
}

section.form form button[type=submit]{
    width: 100%;
    height: 50px;
    background-color: rgb(17, 52, 56);
    border: 0;
    margin-top: 20px;
    font-size: 20px;
    font-weight: bold;
    color: white;
    cursor: pointer;
}
section.form form button[type=submit]:hover{
    background-color: rgb(11, 39, 43);
}
main {
    background-color: #d1d3d4;
    width: 100%;
    color: rgba(255, 255, 255, 0.582);
}


.content {
    display: flex;
    flex-direction: row;
    height: 100%;
}
header {
    display: grid;
    grid-template-columns: 1fr 1fr;
    padding: 10px;
    background-color: rgba(47, 79, 79, 0.849);
}

header h1{
    font-size: 20px;
    font-weight: bold;
    text-align: center;

}
